import { formatDate, intlUtils } from '@navikt/fp-common';
import { AktrType } from 'app/types/AktrType';
import { penBehandling } from 'app/types/penBehandling';
import { TidslinjehendelseType } from 'app/types/TidslinjehendelseType';
import { BehandlingTilstand } from 'app/types/BehandlingTilstand';
import { Tidslinjehendelse } from 'app/types/Tidslinjehendelse';
import dayjs from 'dayjs';
import { IntlShape } from 'react-intl';
import OversiktRoutes, { NavRoutes } from 'app/routes/routes';
import { Uttaksdagen, UTTAKSDAGER_PER_UKE } from 'app/utils/Uttaksdagen';
import { Skjemanummer } from 'app/types/Skjemanummer';
import { Ytelse } from 'app/types/Ytelse';
import { formaterDato } from './dateUtils';

export const VENTERSAKER = [
    BehandlingTilstand.VENTER_P_INNTEKTSMELDING,
    BehandlingTilstand.VENTER_P_DOKUMENTASJON,
    BehandlingTilstand.TIDLIG_SKNAD,
    BehandlingTilstand.VENTER_P_MELDEKORT,
];

export const TIDSLINJEHENDELSER_P_VENT = [
    TidslinjehendelseType.VENTER_INNTEKTSMELDING,
    TidslinjehendelseType.VENTER_MELDEKORT,
    TidslinjehendelseType.VENTER_PGA_TIDLIG_SKNAD,
    TidslinjehendelseType.VENT_DOKUMENTASJON,
];

export const getTidslinjehendelseTittel = (
    hendelsetype: TidslinjehendelseType,
    intl: IntlShape,
    tidlistBehandlingsdato: Date | undefined,
    manglendeVedleggData: Skjemanummer[] | undefined,
    ytelse: Ytelse
): string => {
    if (hendelsetype === TidslinjehendelseType.VENTER_PGA_TIDLIG_SKNAD && tidlistBehandlingsdato !== undefined) {
        return intlUtils(intl, 'tidslinje.tittel.VENTER_PGA_TIDLIG_SKNAD', {
            tidlistBehandlingsdato: formatDate(tidlistBehandlingsdato),
        });
    }
    if (
        hendelsetype === TidslinjehendelseType.VENT_DOKUMENTASJON &&
        manglendeVedleggData &&
        manglendeVedleggData.length === 1
    ) {
        const navnPDokumentasjon = intlUtils(intl, `ettersendelse.${manglendeVedleggData[0]}`);
        const dokumentasjonLowerCase = navnPDokumentasjon.charAt(0).toLowerCase() + navnPDokumentasjon.slice(1);
        return intlUtils(intl, 'tidslinje.navVenterP', { dokumentasjon: dokumentasjonLowerCase });
    }
    if (hendelsetype === TidslinjehendelseType.FRSTEGANGSSKNAD) {
        return intlUtils(intl, 'tidslinje.tittel.FRSTEGANGSSKNAD', { ytelse });
    }
    return intlUtils(intl, `tidslinje.tittel.${hendelsetype}`);
};

export const getTidslinjehendelseStatus = (hendelsetype: TidslinjehendelseType, hendelsesDato: Date) => {
    if (TIDSLINJEHENDELSER_P_VENT.includes(hendelsetype)) {
        return 'warning';
    }
    if (dayjs(hendelsesDato).isSameOrBefore(new Date())) {
        return 'completed';
    }
    return 'incomplete';
};

export const getTidslinjeHendelstypeAvVentersak = (ventersak: BehandlingTilstand) => {
    if (ventersak === BehandlingTilstand.VENTER_P_INNTEKTSMELDING) {
        return TidslinjehendelseType.VENTER_INNTEKTSMELDING;
    }
    if (ventersak === BehandlingTilstand.TIDLIG_SKNAD) {
        return TidslinjehendelseType.VENTER_PGA_TIDLIG_SKNAD;
    }
    if (ventersak === BehandlingTilstand.VENTER_P_DOKUMENTASJON) {
        return TidslinjehendelseType.VENT_DOKUMENTASJON;
    }
    if (ventersak === BehandlingTilstand.VENTER_P_MELDEKORT) {
        return TidslinjehendelseType.VENTER_MELDEKORT;
    } else {
        throw new Error('Ukjent ventersak');
    }
};

export const getAktrtypeAvVentersak = (ventersak: BehandlingTilstand): AktrType => {
    if (ventersak === BehandlingTilstand.VENTER_P_INNTEKTSMELDING) {
        return AktrType.ARBEIDSGIVER;
    }
    if (ventersak === BehandlingTilstand.TIDLIG_SKNAD) {
        return AktrType.NAV;
    }
    return AktrType.BRUKER;
};

export const getTidlinjeHendelseEksternUrl = (ventersak: BehandlingTilstand): NavRoutes | undefined => {
    if (ventersak === BehandlingTilstand.VENTER_P_INNTEKTSMELDING) {
        return NavRoutes.VENT_INNTEKTSMELDING;
    }
    if (ventersak === BehandlingTilstand.TIDLIG_SKNAD) {
        return NavRoutes.VENT_INNTEKTSMELDING;
    }

    if (ventersak === BehandlingTilstand.VENTER_P_MELDEKORT) {
        return NavRoutes.VENT_MELDEKORT;
    }
    return undefined;
};

export const getTidligstBehandlingsDatoForTidligSknad = (penBehandling: penBehandling): Date => {
    const sknadsperioder = penBehandling.sknadsperioder;
    const frsteUttaksdagISaken = dayjs(sknadsperioder![0].fom).toDate();
    return Uttaksdagen(frsteUttaksdagISaken).trekkFra(4 * UTTAKSDAGER_PER_UKE);
};

const getDatoForInnsendingAvFrsteSknad = (tidslinjeHendelser: Tidslinjehendelse[]): Date | undefined => {
    const hendelseFrsteSknad = tidslinjeHendelser.find(
        (hendelse) => hendelse.tidslinjeHendelseType === TidslinjehendelseType.FRSTEGANGSSKNAD
    );
    return hendelseFrsteSknad ? hendelseFrsteSknad.opprettet : undefined;
};

export const getTidslinjehendelserDetaljer = (
    tidslinjeHendelserData: Tidslinjehendelse[],
    intl: IntlShape
): Tidslinjehendelse[] => {
    return tidslinjeHendelserData.map((hendelse) => {
        switch (hendelse.tidslinjeHendelseType) {
            case TidslinjehendelseType.UTGENDE_INNHENT_OPPLYSNINGER:
                return {
                    ...hendelse,
                    internalUrl: OversiktRoutes.ETTERSEND,
                    linkTittel: intlUtils(intl, 'tidslinje.VENT_DOKUMENTASJON.linkTittel'),
                };
            case TidslinjehendelseType.FRSTEGANGSSKNAD_NY: {
                const datoFrsteSknad = getDatoForInnsendingAvFrsteSknad(tidslinjeHendelserData);
                return {
                    ...hendelse,
                    merInformasjon: datoFrsteSknad
                        ? intlUtils(intl, 'tidslinje.merInformasjon.FRSTEGANGSSKNAD_NY', {
                              datoFrsteSknad: formaterDato(datoFrsteSknad, 'DD. MMM YYYY'),
                          })
                        : intlUtils(intl, 'tidslinje.merInformasjon.FRSTEGANGSSKNAD_NY.ukjentDatoFrstSknad'),
                };
            }
            default:
                return hendelse;
        }
    });
};

export const getTidslinjehendelserFraBehandlingPVent = (
    penBehandling: penBehandling,
    manglendeVedleggData: Skjemanummer[],
    intl: IntlShape
): Tidslinjehendelse[] => {
    let hendelseVenterPDokumentasjon = undefined;
    if (
        penBehandling.tilstand === BehandlingTilstand.VENTER_P_INNTEKTSMELDING &&
        manglendeVedleggData &&
        manglendeVedleggData.length > 0
    ) {
        const behandlingTilstand = BehandlingTilstand.VENTER_P_DOKUMENTASJON;
        hendelseVenterPDokumentasjon = {
            type: 'sknad',
            opprettet: new Date(),
            aktrType: getAktrtypeAvVentersak(penBehandling.tilstand),
            tidslinjeHendelseType: getTidslinjeHendelstypeAvVentersak(behandlingTilstand),
            dokumenter: [],
            manglendeVedlegg: [],
            merInformasjon: intlUtils(intl, `tidslinje.${behandlingTilstand}.informasjon`),
            linkTittel: intlUtils(intl, `tidslinje.${behandlingTilstand}.linkTittel`),
            eksternalUrl: getTidlinjeHendelseEksternUrl(behandlingTilstand),
            internalUrl: OversiktRoutes.ETTERSEND,
            tidligstBehandlingsDato: undefined,
        };
    }
    const tidslinjeHendelse = {
        type: 'sknad',
        opprettet: new Date(),
        aktrType: getAktrtypeAvVentersak(penBehandling.tilstand),
        tidslinjeHendelseType: getTidslinjeHendelstypeAvVentersak(penBehandling.tilstand),
        dokumenter: [],
        manglendeVedlegg: [],
        merInformasjon: intlUtils(intl, `tidslinje.${penBehandling.tilstand}.informasjon`),
        linkTittel: intlUtils(intl, `tidslinje.${penBehandling.tilstand}.linkTittel`),
        eksternalUrl: getTidlinjeHendelseEksternUrl(penBehandling.tilstand),
        internalUrl:
            penBehandling.tilstand === BehandlingTilstand.VENTER_P_DOKUMENTASJON
                ? OversiktRoutes.ETTERSEND
                : undefined,
        tidligstBehandlingsDato:
            penBehandling.tilstand === BehandlingTilstand.TIDLIG_SKNAD
                ? getTidligstBehandlingsDatoForTidligSknad(penBehandling)
                : undefined,
    };

    if (hendelseVenterPDokumentasjon) {
        return [tidslinjeHendelse, hendelseVenterPDokumentasjon];
    }

    return [tidslinjeHendelse];
};

export const sorterTidslinjehendelser = (h1: Tidslinjehendelse, h2: Tidslinjehendelse) => {
    if (dayjs(h1.opprettet).isBefore(h2.opprettet)) {
        return -1;
    } else if (dayjs(h1.opprettet).isAfter(h2.opprettet)) {
        return 1;
    } else {
        return 0;
    }
};
