import { MorsAktivitet } from 'app/types/MorsAktivitet';
import { PeriodeResultat } from 'app/types/PeriodeResultat';
import { StnadskontoType } from 'app/types/StnadskontoType';
import { getStnadskontoForelderNavn } from 'app/utils/periodeUtils';
import { NavnPForeldre } from 'app/utils/personUtils';
import { getStnadskontoFarge } from 'app/utils/styleUtils';
import React, { FunctionComponent } from 'react';
import { useIntl } from 'react-intl';

import IconBox from '../icon-box/IconBox';
import UttaksplanIkon, { UttaksplanIkonKeys } from '../uttaksplan-ikon/UttaksplanIkon';

export interface Props {
    erAleneOmOmsorg?: boolean;
    erFarEllerMedmor?: boolean;
    gradert?: boolean;
    konto: StnadskontoType;
    morsAktivitet: MorsAktivitet | undefined;
    navnPForeldre: NavnPForeldre;
    periodeResultat: PeriodeResultat | undefined;
}

const StnadskontoIkon: FunctionComponent<Props> = ({
    konto,
    gradert,
    navnPForeldre,
    periodeResultat,
    morsAktivitet,
    erFarEllerMedmor,
    erAleneOmOmsorg,
}) => {
    const intl = useIntl();

    return (
        <IconBox color={getStnadskontoFarge(konto)} stripes={gradert}>
            <UttaksplanIkon
                ikon={UttaksplanIkonKeys.uttak}
                title={getStnadskontoForelderNavn(
                    intl,
                    konto,
                    navnPForeldre,
                    periodeResultat,
                    morsAktivitet,
                    erFarEllerMedmor,
                    erAleneOmOmsorg
                )}
            />
        </IconBox>
    );
};

export default StnadskontoIkon;
