import { EngangsstnadSak, EngangsstnadSakDTO } from 'app/types/EngangsstnadSak';
import { Familiehendelse } from 'app/types/Familiehendelse';
import { GruppertSak } from 'app/types/GruppertSak';
import { BarnGruppering } from 'app/types/BarnGruppering';
import { Foreldrepengesak, ForeldrepengesakDTO } from 'app/types/Foreldrepengesak';
import { SakOppslag, SakOppslagDTO } from 'app/types/SakOppslag';
import { SvangerskapspengeSak, SvangerskapspengeSakDTO } from 'app/types/SvangerskapspengeSak';
import { Ytelse } from 'app/types/Ytelse';
import dayjs from 'dayjs';
import { SkerinfoDTO } from 'app/types/SkerinfoDTO';
import { Sak } from 'app/types/Sak';
import { Person } from 'app/types/Person';
import { getErDatoInnenEnDagFraAnnenDato, ISOStringToDate } from './dateUtils';
import { getLeverPerson } from './personUtils';
import { IntlShape } from 'react-intl';
import { formatDate, intlUtils } from '@navikt/fp-common';
import { Situasjon } from 'app/types/Situasjon';

export const getAlleYtelser = (saker: SakOppslag): Sak[] => {
    return [...saker.engangsstnad, ...saker.foreldrepenger, ...saker.svangerskapspenger];
};

export function sorterPersonEtterEldstOgNavn(p1: Person, p2: Person) {
    if (dayjs(p1.fdselsdato).isAfter(p2.fdselsdato, 'd')) {
        return 1;
    } else if (dayjs(p1.fdselsdato).isBefore(p2.fdselsdato, 'd')) {
        return -1;
    } else {
        return p1.fornavn < p2.fornavn ? -1 : 1;
    }
}

const getBarnGrupperingFraSak = (sak: Sak, registrerteBarn: Person[] | undefined): BarnGruppering => {
    const erForeldrepengesak = sak.ytelse === Ytelse.FORELDREPENGER;
    const barnFnrFraSaken = erForeldrepengesak && sak.barn !== undefined ? sak.barn.map((b) => b.fnr).flat() : [];
    const pdlBarnMedSammeFnr =
        erForeldrepengesak && registrerteBarn ? registrerteBarn.filter((b) => barnFnrFraSaken.includes(b.fnr)) : [];
    const fdselsdatoFraSak = ISOStringToDate(sak.familiehendelse.fdselsdato);
    const pdlBarnMedSammeFdselsdato =
        fdselsdatoFraSak !== undefined && registrerteBarn
            ? registrerteBarn.filter(
                  (barn) =>
                      getErDatoInnenEnDagFraAnnenDato(ISOStringToDate(barn.fdselsdato), fdselsdatoFraSak) &&
                      !pdlBarnMedSammeFnr?.find((pdlBarn) => pdlBarn.fnr === barn.fnr)
              )
            : [];

    const alleBarn = pdlBarnMedSammeFnr.concat(pdlBarnMedSammeFdselsdato);
    alleBarn.sort(sorterPersonEtterEldstOgNavn);
    const alleBarnFdselsdatoer = alleBarn
        .filter((b) => b.fdselsdato !== undefined)
        .map((b) => ISOStringToDate(b.fdselsdato)!);
    let fdselsdatoer = [] as Date[];
    if (alleBarnFdselsdatoer && alleBarnFdselsdatoer.length > 0) {
        fdselsdatoer = alleBarnFdselsdatoer;
    } else if (fdselsdatoFraSak) {
        fdselsdatoer = [fdselsdatoFraSak];
    }

    return {
        fornavn: alleBarn
            ?.filter((b) => b.fornavn !== undefined && b.fornavn.trim() !== '')
            .map((b) => [b.fornavn, b.mellomnavn !== undefined ? b.mellomnavn : ''].join(' ')),
        fdselsdatoer,
        alleBarnaLever: !!alleBarn?.every((barn) => getLeverPerson(barn)),
    };
};

export const grupperSakerPBarn = (registrerteBarn: Person[] | undefined, saker: SakOppslag): GruppertSak[] => {
    const alleSaker = getAlleYtelser(saker);

    return alleSaker.reduce((result, sak) => {
        const familiehendelsedato = getFamiliehendelseDato(sak.familiehendelse);
        const relevantSak = result.find((gruppertSak) => findRelevantSak(gruppertSak, familiehendelsedato));

        if (relevantSak) {
            relevantSak.saker.push(sak);
        }

        if (relevantSak && result.includes(relevantSak)) {
            return result;
        } else {
            const type = utledFamiliesituasjon(sak.familiehendelse, sak.gjelderAdopsjon);
            const gruppertSak: GruppertSak = {
                antallBarn: sak.familiehendelse.antallBarn,
                familiehendelsedato,
                saker: [sak],
                type,
                ytelse: sak.ytelse,
                barn: type !== 'termin' ? getBarnGrupperingFraSak(sak, registrerteBarn) : undefined,
            };

            result.push(gruppertSak);

            return result;
        }
    }, [] as GruppertSak[]);
};

const addYtelseToSak = (
    saker: ForeldrepengesakDTO[] | SvangerskapspengeSakDTO[] | EngangsstnadSakDTO[],
    ytelse: Ytelse
): Foreldrepengesak[] | SvangerskapspengeSak[] | EngangsstnadSak[] => {
    if (ytelse === Ytelse.ENGANGSSTNAD) {
        return saker.map(
            (sak) =>
                ({
                    ...sak,
                    ytelse,
                } as EngangsstnadSak)
        );
    }

    if (ytelse === Ytelse.SVANGERSKAPSPENGER) {
        return saker.map(
            (sak) =>
                ({
                    ...sak,
                    ytelse,
                } as SvangerskapspengeSak)
        );
    }

    return saker.map(
        (sak) =>
            ({
                ...sak,
                ytelse,
            } as Foreldrepengesak)
    );
};

export const mapSakerDTOToSaker = (saker: SakOppslagDTO): SakOppslag => {
    return {
        foreldrepenger: addYtelseToSak(saker.foreldrepenger, Ytelse.FORELDREPENGER) as Foreldrepengesak[],
        engangsstnad: addYtelseToSak(saker.engangsstnad, Ytelse.ENGANGSSTNAD) as EngangsstnadSak[],
        svangerskapspenger: addYtelseToSak(
            saker.svangerskapspenger,
            Ytelse.SVANGERSKAPSPENGER
        ) as SvangerskapspengeSak[],
    };
};

export const getAntallSaker = (saker: SakOppslag) => {
    const { foreldrepenger, svangerskapspenger, engangsstnad } = saker;
    return foreldrepenger.length + svangerskapspenger.length + engangsstnad.length;
};

const findRelevantSak = (gruppertSak: GruppertSak, familiehendelsedato: string) => {
    const startdato = dayjs(familiehendelsedato).subtract(2, 'months');
    const sluttdato = dayjs(familiehendelsedato).add(3, 'weeks');

    if (gruppertSak) {
        return (
            dayjs(gruppertSak.familiehendelsedato).isAfter(startdato) &&
            dayjs(gruppertSak.familiehendelsedato).isSameOrBefore(sluttdato)
        );
    }

    return undefined;
};

export const utledFamiliesituasjon = (familiehendelse: Familiehendelse, gjelderAdopsjon: boolean) => {
    if (gjelderAdopsjon) {
        return 'adopsjon';
    }

    const { fdselsdato } = familiehendelse;

    if (fdselsdato) {
        return 'fdsel';
    }

    return 'termin';
};

export const getFamiliehendelseDato = (familiehendelse: Familiehendelse): string => {
    const { fdselsdato, termindato, omsorgsovertakelse } = familiehendelse;

    if (omsorgsovertakelse) {
        return omsorgsovertakelse;
    }

    if (fdselsdato) {
        return fdselsdato;
    }

    return termindato!;
};

export const getNavnAnnenForelder = (
    skerinfo: SkerinfoDTO,
    sak: Foreldrepengesak | EngangsstnadSak | SvangerskapspengeSak | undefined
) => {
    const fdselsdatoFraSak = sak ? sak.familiehendelse.fdselsdato : undefined;
    const barn =
        skerinfo.sker.barn && fdselsdatoFraSak
            ? skerinfo.sker.barn.find((b) => dayjs(b.fdselsdato).isSame(fdselsdatoFraSak, 'd'))
            : undefined;
    const annenForelderNavn = barn && barn.annenForelder ? barn.annenForelder.fornavn : undefined;
    return annenForelderNavn !== undefined && annenForelderNavn.trim() !== '' ? annenForelderNavn : 'Annen forelder';
};

export const getTekstForAntallBarn = (antallBarn: number, intl: IntlShape): string => {
    if (antallBarn === 1 || antallBarn === 0) {
        return intlUtils(intl, 'barn');
    } else if (antallBarn === 2) {
        return intlUtils(intl, 'tvillinger');
    } else if (antallBarn === 3) {
        return intlUtils(intl, 'trillinger');
    }
    return intlUtils(intl, 'flerlinger');
};

export const formaterFdselsdatoerPBarn = (fdselsdatoer: Date[] | undefined): string | undefined => {
    if (fdselsdatoer === undefined) {
        return undefined;
    }
    const unikeFdselsdatoer = [] as Date[];
    fdselsdatoer.forEach((f) => {
        const finnesIUnikeFdselsdatoer = unikeFdselsdatoer.find((dato) => dayjs(dato).isSame(f, 'day'));
        if (finnesIUnikeFdselsdatoer === undefined) {
            unikeFdselsdatoer.push(f);
        }
    });

    if (unikeFdselsdatoer.length > 1) {
        const fdselsdatoerTekst = unikeFdselsdatoer.map((fd) => formatDate(fd));
        const frsteFdselsdaoer = fdselsdatoerTekst.slice(0, -1).join(', ');
        const sisteFdselsdato = fdselsdatoerTekst[fdselsdatoerTekst.length - 1];
        return `${frsteFdselsdaoer} og ${sisteFdselsdato}`;
    }
    return formatDate(unikeFdselsdatoer[0]);
};

export const getTittelBarnNrNavnSkalIkkeVises = (
    familiehendelsedato: Date,
    fdselsdatoer: Date[] | undefined,
    antallBarn: number,
    intl: IntlShape,
    type: Situasjon
): string => {
    const barnTekst = getTekstForAntallBarn(antallBarn, intl);
    if ((antallBarn === 0 && fdselsdatoer === undefined) || type === 'termin') {
        return intlUtils(intl, 'barnHeader.terminBarn', {
            barnTekst,
            termindato: formatDate(familiehendelsedato),
        });
    }

    if (type === 'adopsjon') {
        return intlUtils(intl, 'barnHeader.adoptertBarn', {
            adopsjonsdato: formatDate(familiehendelsedato),
        });
    } else {
        const fdselsdatoTekst = formaterFdselsdatoerPBarn(fdselsdatoer);
        return fdselsdatoer !== undefined && fdselsdatoer.length > 0
            ? intlUtils(intl, 'barnHeader.fdtBarn', {
                  barnTekst,
                  fdselsdatoTekst,
              })
            : '';
    }
};

export const getSakTittel = (
    fornavn: string[] | undefined,
    fdselsdatoer: Date[] | undefined,
    familiehendelsesdato: Date,
    alleBarnaLever: boolean,
    antallBarn: number,
    intl: IntlShape,
    type: Situasjon
): string => {
    if (fornavn === undefined || fornavn.length === 0 || !alleBarnaLever) {
        return getTittelBarnNrNavnSkalIkkeVises(familiehendelsesdato, fdselsdatoer, antallBarn, intl, type);
    }

    if (fornavn.length > 1) {
        const fornavnene = fornavn
            .map((n) => n.trim())
            .slice(0, -1)
            .join(', ');
        const sisteFornavn = fornavn[fornavn.length - 1];
        return `${fornavnene} og ${sisteFornavn}`;
    }
    return `${fornavn[0]}`;
};

export const getSakUndertittel = (
    fornavn: string[] | undefined,
    fdselsdatoer: Date[] | undefined,
    type: Situasjon,
    familiehendelsedato: Date,
    allebarnaLever: boolean
) => {
    const viserNavnPBarn = fornavn !== undefined && fornavn.length > 0 && allebarnaLever;
    if (type === 'fdsel' && viserNavnPBarn) {
        const fdtDatoTekst = formaterFdselsdatoerPBarn(fdselsdatoer);
        return `Fdt ${fdtDatoTekst}`;
    }
    if (type === 'adopsjon' && viserNavnPBarn) {
        return `Adoptert ${formatDate(familiehendelsedato)}`;
    }
    return undefined;
};
