import { StnadskontoType } from 'app/types/StnadskontoType';
import { UttaksplanColor } from 'app/types/UttaksplanColor';

export const getStnadskontoFarge = (konto: StnadskontoType): UttaksplanColor => {
    switch (konto) {
        case StnadskontoType.Fedrekvote:
        case StnadskontoType.AktivitetsfriKvote:
            return UttaksplanColor.blue;
        case StnadskontoType.Mdrekvote:
        case StnadskontoType.Foreldrepenger:
        case StnadskontoType.ForeldrepengerFrFdsel:
            return UttaksplanColor.purple;
        case StnadskontoType.Fellesperiode:
            return UttaksplanColor.purpleBlue;
        default:
            return UttaksplanColor.transparent;
    }
};
