import { Tag } from '@navikt/ds-react';
import { BehandlingTilstand } from 'app/types/BehandlingTilstand';
import { Sak } from 'app/types/Sak';
import React from 'react';

interface Props {
    className?: string;
    sak: Sak;
}

const StatusTag: React.FunctionComponent<Props> = ({ sak, className }) => {
    if (sak.penBehandling) {
        if (!sak.sakAvsluttet) {
            if (sak.penBehandling.tilstand === BehandlingTilstand.UNDER_BEHANDLING) {
                return (
                    <Tag className={className} variant="warning">
                        Under behandling
                    </Tag>
                );
            }

            if (sak.penBehandling.tilstand === BehandlingTilstand.VENTER_P_INNTEKTSMELDING) {
                return (
                    <Tag className={className} variant="warning">
                        Venter p inntektsmelding fra arbeidsgiver
                    </Tag>
                );
            }

            if (sak.penBehandling.tilstand === BehandlingTilstand.VENTER_P_DOKUMENTASJON) {
                return (
                    <Tag className={className} variant="warning">
                        Venter p dokumentasjon
                    </Tag>
                );
            }

            if (sak.penBehandling.tilstand === BehandlingTilstand.TIDLIG_SKNAD) {
                return (
                    <Tag className={className} variant="warning">
                        Sknaden vil bli behandlet senere
                    </Tag>
                );
            }

            if (sak.penBehandling.tilstand === BehandlingTilstand.VENTER_P_MELDEKORT) {
                return (
                    <Tag className={className} variant="warning">
                        Venter p meldekort
                    </Tag>
                );
            }
        }
    }

    if (sak.sakAvsluttet) {
        return (
            <Tag className={className} variant="success">
                Avsluttet
            </Tag>
        );
    }

    return (
        <Tag className={className} variant="success">
            Aktiv
        </Tag>
    );
};

export default StatusTag;
