import { Foreldrepengesak } from 'app/types/Foreldrepengesak';
import { Person } from 'app/types/Person';

export interface NavnPForeldre {
    farMedmor: string;
    mor: string;
}

const navnSlutterPSLyd = (navn: string): boolean => {
    const sisteBokstav = navn.charAt(navn.length - 1).toLowerCase();
    return sisteBokstav === 's' || sisteBokstav === 'x' || sisteBokstav === 'z';
};

export const getNavnGenitivEierform = (navn: string, locale: string): string => {
    if (locale !== 'nb') {
        return navn;
    }
    if (navnSlutterPSLyd(navn)) {
        return `${navn}'`;
    }
    return `${navn}s`;
};

export const getNavnPForeldre = (
    sak: Foreldrepengesak,
    navnPSker: string,
    navnAnnenForelder: string
): NavnPForeldre => {
    const skerErFarEllerMedmor = !sak.sakTilhrerMor;
    return {
        farMedmor: skerErFarEllerMedmor ? navnPSker : navnAnnenForelder,
        mor: skerErFarEllerMedmor ? navnAnnenForelder : navnPSker,
    };
};

export const getLeverPerson = (person: Person) => {
    return !person.ddsdato;
};
