import { intlUtils } from '@navikt/fp-common';
import React from 'react';
import { useIntl } from 'react-intl';
import IconBox from '../icon-box/IconBox';
import { UtsettelsersakType } from 'app/types/UtsettelsersakType';
import UttaksplanIkon, { UttaksplanIkonKeys } from '../uttaksplan-ikon/UttaksplanIkon';
import { UttaksplanColor } from 'app/types/UttaksplanColor';

export const getUtsettelseFarge = (): UttaksplanColor => {
    return UttaksplanColor.green;
};

export interface Props {
    rsak: UtsettelsersakType;
}

const getIkonForrsak = (rsak: UtsettelsersakType): UttaksplanIkonKeys => {
    switch (rsak) {
        case UtsettelsersakType.Ferie:
            return UttaksplanIkonKeys.ferie;
        case UtsettelsersakType.Arbeid:
        case UtsettelsersakType.Hvvelse:
        case UtsettelsersakType.NavTiltak:
        case UtsettelsersakType.Fri:
            return UttaksplanIkonKeys.arbeid;
        case UtsettelsersakType.InstitusjonBarnet:
        case UtsettelsersakType.InstitusjonSker:
        case UtsettelsersakType.Sykdom:
            return UttaksplanIkonKeys.sykdom;
    }
};

const UtsettelseIkon: React.FunctionComponent<Props> = ({ rsak }) => {
    const intl = useIntl();

    return (
        <IconBox color={getUtsettelseFarge()}>
            <UttaksplanIkon
                ikon={getIkonForrsak(rsak)}
                title={intlUtils(intl, `uttaksplan.utsettelsesrsak.${rsak || 'ukjent'}`)}
            />
        </IconBox>
    );
};

export default UtsettelseIkon;
